@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
*{margin:0;padding:0;font-family:'Permanent Marker', cursive;font-size:1rem;box-sizing:border-box}a{color:#fff}.page{padding:1rem}.header{display:flex;justify-content:space-between;align-items:center;margin-bottom:2rem;padding-bottom:0.5rem;border-bottom:1px solid #eee}.header__title{font-size:1.5rem;color:#9c8cd4;padding-right:35px}.header__search{padding:0.5rem;width:20rem;border:2px solid #ddd;border-radius:0.5rem;background-color:#f8f8f8;outline:none;transition:all ease 0.3s}.header__search:focus{border-color:#9c8cd4;background-color:#fff}.quote__list{display:flex;justify-content:space-between;flex-wrap:wrap}.quote__item{margin-bottom:1rem;padding:1rem;width:20rem;list-style:none;border-radius:0.5rem;border:1px solid #9c8cd4;background-color:#9c8cd4;box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px;font-weight:normal;color:#f6df80}.quote__item>*{margin:3px 0}.quote__label{display:inline-block;font-weight:normal;width:5rem;color:#fff}.new-quote__title{padding:3rem 0 1rem 0;font-size:1.5rem;color:#9c8cd4}.new-quote__input{margin-right:1rem;margin-bottom:1rem;padding:0.5rem;width:10rem;border:2px solid #ddd;border-radius:0.5rem;background-color:#f8f8f8;outline:none;transition:all ease 0.3s}.new-quote__input:focus{border-color:#9c8cd4;background-color:#fff;box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px}.new-quote__btn{padding:0.5rem 1.5rem;border-radius:0.5rem;border:none;border:2px solid   #9c8cd4;background-color:#9c8cd4;font-weight:bold;color:#f6df80;cursor:pointer;transition:all ease 0.3s}.new-quote__btn:hover{border-color:#9c8cd4;background-color:#33b8ab}

